<template>
	<section class="scene-hero" ref="sceneHero" :class="[className, inView ? 'active-pointer' : '']">
		<a href="#video-click" class="video-click" @click="handleVideoClick" v-if="options.contentId == 'brandedVideo'"></a>
		
		<SceneHeadline :headline="options.titleId" :manchet="options.manchetId" ref="headline" :firstHero="firstHero"/>
		<div class="scroll-indicator" v-if="options.name == 'butterfly-hero'">
		<!-- <div class="scroll-indicator"> -->
			<div class="arrow-wrapper" ref="arrowWrapper">
				<div class="arrow-circle" ref="arrowCircle"></div>
				<svg class="arrow-down arrow-down1" ref="arrow1" width="22px" height="36px" viewBox="0 0 22 36" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
					<g id="Design" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round">
						<g id="Desktop-1920x1080" transform="translate(-949.000000, -1987.000000)" stroke="#FFFFFF" stroke-width="2">
							<g id="Arrow_down" transform="translate(950.000000, 1988.000000)">
								<polyline id="Stroke-4" points="0.0018 24.0369 10.0018 34.0369 20.0018 24.0369"></polyline>
								<path d="M10.0018,34.0369 L10.0018,-0.0001"></path>
							</g>
						</g>
					</g>
				</svg>
				<svg class="arrow-down arrow-down2" ref="arrow2" width="22px" height="36px" viewBox="0 0 22 36" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
					<g id="Design" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round">
						<g id="Desktop-1920x1080" transform="translate(-949.000000, -1987.000000)" stroke="#FFFFFF" stroke-width="2">
							<g id="Arrow_down" transform="translate(950.000000, 1988.000000)">
								<polyline id="Stroke-4" points="0.0018 24.0369 10.0018 34.0369 20.0018 24.0369"></polyline>
								<path d="M10.0018,34.0369 L10.0018,-0.0001"></path>
							</g>
						</g>
					</g>
				</svg>
			</div>
		</div>
		<!--
			HERO Not in use since introducing only one video tag at top level
		-->
		<!-- <Hero :type="options.heroType" :src="options.heroSrc" ref="video"/> -->
	</section>
</template>

<script>
import { MapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

// import GSDevTools from 'GSDevTools';
// import SplitText from 'GSSplitText';

import Hero from '../Hero';
import SceneHeadline from '../SceneHeadline';

export default {
	name: 'SceneHero',
	mixins: [ProgressMixin],
	components: {
		Hero,
		SceneHeadline,
	},
	props: {
		heroType: {
			type: String,
			default: 'video',
		},
		heroSrc: {
			type: String,
		},
		headline: {
			type: String,
		},
		className: String,
		options: Object,
		firstHero: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			mq: this.$store.state.mq,
			tl: null,
		};
	},
	created(){
		if(this.options.contentId === 'butterflyHero'){
			this.introTl = null; // outroTl is used instead
			this.tl = new TimelineMax({ paused: true }); // headline intro animation (overwrite mixin default)
		} else {
			this.outroTl = null; // reset what comes from mixin to disable outro animation
		}
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
		// console.log('scene name: ', this.options.name);

		this.heroVideo = document.querySelector('#main-video');

		this.$refs.sceneHero.style.height = this.$store.state.windowHeight;
	},

	methods: {
		handleVideoClick(){
			console.log('video click..................');
			if(this.heroVideo.muted){
				this.heroVideo.muted = false;
			} else {
				this.heroVideo.muted = true;
			}
			// this.heroVideo.volume = 1.0;
		},
		playIntro(){ // called by app.vue if state.showIntro is false
			// this.tl.tweenFromTo('start', 'end');
			this.tl.play();
			// this.runPrepare();
			// this.introTl.play();
			// GSDevTools.create({id: 'dsadas'});
		},
		setupScene(){
			if(this.options.contentId === 'butterflyHero'){
				this.setupFirstHeroAnimations();
			} else {
				this.setupAnimations();
			}
		},
		runPrepare(prepareProgress){
			// COMMENT: Used when only one video-tag
			const currentSrc = this.heroVideo.src;
			if(typeof this.options.heroSrc === 'string'){
				if(currentSrc !== this.options.heroSrc){
					this.heroVideo.src = this.options.heroSrc;
				}
			} else {
				if(this.mq.portrait === true && this.mq.md === false){
					if(currentSrc !== this.options.heroSrc.mobile){
						this.heroVideo.src = this.options.heroSrc.mobile;
					}
				} else if(this.mq.xxl === true){
					if(currentSrc !== this.options.heroSrc.desktopLarge){
						this.heroVideo.src = this.options.heroSrc.desktopLarge;
					}
				} else if(this.mq.md === true){
					if(currentSrc !== this.options.heroSrc.desktopSmall){
						this.heroVideo.src = this.options.heroSrc.desktopSmall;
					}
				}
			}
			this.heroVideo.play();

			// COMMENT: Used with multiple video-tags
			// var oldVideoContainer = document.querySelector('.video-active');
			// oldVideoContainer.pause();
			// oldVideoContainer.classList.remove('video-active');
			// var newVideoContainer = document.querySelector(this.options.videoContainer);
			// newVideoContainer.play();
			// newVideoContainer.classList.add('video-active');
		},
		setupFirstHeroAnimations(){ // Butterfly Hero
			var bg = document.querySelector('.bg--intro');
			var introDuration = 2;

			// ------------------
			// ------------- NOTE
			// STRICT order in defining the gsap timelines(outro before tl), in order to get the right "first print" animation state!
			// ------------------

			const arrowWrapper = this.$refs.arrowWrapper; // for the outro
			const arrows = [this.$refs.arrow1, this.$refs.arrow2];
			const arrowCircle = this.$refs.arrowCircle;
			this.arrowTl = new TimelineMax({ paused: false });
			
			// function arrowAnimRepeat(){
			// 	// this.arrowTl.seek('repeatPoint').play().removePause().addPause();
			// 	this.arrowTl.seek('repeatPoint').play();
			// }

			this.arrowTl
				.to(arrowCircle, 0.5,
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.fromTo(arrows[0], 1,
					{
						y: '-120%',
						x: '-50%',
						autoAlpha: 0,
					},
					{
						y: '-50%',
						x: '-50%',
						autoAlpha: 1,
						delay: -0.2,
						ease: Power2.easeInOut,
					}
				)
				.add('repeatPoint')
				.to(arrows[0], .5,
					{
						autoAlpha: 0,
					},
				)
				.fromTo(arrows[1], 1,
					{
						y: '-120%',
						x: '-50%',
						autoAlpha: 0,
					},
					{
						delay: -0.4,
						y: '-50%',
						x: '-50%',
						autoAlpha: 1,
						ease: Power2.easeInOut,
					}
				)
				.to(arrows[1], .5,
					{
						autoAlpha: 0,
					},
				)
				.fromTo(arrows[0], 1,
					{
						y: '-120%',
						x: '-50%',
						autoAlpha: 0,
					},
					{
						delay: -0.4,
						y: '-50%',
						x: '-50%',
						autoAlpha: 1,
						ease: Power2.easeInOut,
					}
				)
				.add('repeatEnd')
				// .tweenFromTo('repeatPoint', 'repeatEnd')
				// .seek('repeatPoint')
				// .add(arrowAnimRepeat, '+=1')
				// .to(arrows[0], .5,
				// 	{
				// 		y: '0%',
				// 		autoAlpha: 1,
				// 		ease: Power4.easeIn,
				// 	}
				// )
				;


			this.outroTl
				.add('start')
				.fromTo(this.$refs.headline.$el, 1,
					{ 
						y: '-50%',
						autoAlpha: 1,
					},
					{ 
						y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
						autoAlpha: 0,
						ease: Linear.easeNone,
					}
				)
				.to(arrowWrapper, 0.5,
					{
						autoAlpha: 0,
					},
					'start+=0'
				)
				.add('end')
				.progress(1) // caching animation
				.progress(0) // set stort pos back to 0
				// .pause()
				;
			

			// ------------------
			// ------------- NOTE
			// STRICT order in defining the gsap timelines(outro before tl), in order to get the right "first print" animation state!
			// ------------------

			this.tl
				.add('start')
				.fromTo(this.$refs.headline.$el, introDuration, 
					{ 
						y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
						autoAlpha: 0,
					},
					{ 
						y: '-50%',
						autoAlpha: 1,
						// ease: Linear.easeNone,
						ease: Power2.easeInOut,
					})
				.fromTo(
					bg,
					1.5, 
					{ 
						// y: '-=' + ((this.windowHeight / 100) * this.headlineAnimationDistance) + 'px',
						autoAlpha: 1,
						// delay: -introDuration,
					}, 
					{ 
						// y: '-=' + ((this.windowHeight / 100) * this.headlineAnimationDistance) + 'px',
						autoAlpha: 0,
						// delay: -introDuration,
						ease: Linear.easeNone,
					},
					'start'
				)
				.add('end')
				.add(this.arrowTl, 'end+=0.5', 'arrows')
				.progress(1) // caching animation
				.progress(0) // set stort pos back to 0
				// .pause()
				;

			this.tl
				.addCallback(() => {
					if(this.$store.state.introHijackScroll === true){
						document.body.style.height = this.$store.state.masterHeight + 'px';
						document.body.style.maxHeight = 'none';
						document.body.style.overflow = 'auto';
					}
				}, 'end');

			// console.log('butter hero tl: ', this.tl);
		},
		setupAnimations(){
			var progress = 0;
			var scrollDirection = 'down';
			var bg = document.querySelector('.bg--main');

			this.introTl = new TimelineMax({paused: true, onUpdateParams: ['{self}'], onUpdate: function(introTl) {
				var currentProgress = introTl.progress();﻿
				if(currentProgress > progress){
					scrollDirection = 'down'
				} else {
					scrollDirection = 'up'
				}
				progress = introTl.progress();﻿
			}});

			// ------------ STANDARD HEADLINE ANIM
			// this.introTl
			// 	.add('start')
			// 	// ----- Linear easing
			// 	.fromTo(this.$refs.headline.$el, 1, 
			// 		{ 
			// 			y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
			// 			autoAlpha: 0,
			// 		},
			// 		{ 
			// 			y: '-50%',
			// 			autoAlpha: 1,
			// 			ease: Power2.easeIn,
			// 			// ease: Linear.easeNone,
			// 		}
			// 	)
			// 	.add('50')
			// 	.to(this.$refs.headline.$el, 1,
			// 		{ 
			// 			y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
			// 			autoAlpha: 0,
			// 			delay: 1,
			// 			ease: Power2.easeIn,
			// 			// ease: Linear.easeNone,
			// 		}
			// 	)
			// 	.add('end')
			// 	.progress(1) // caching animation
			// 	.progress(0) // set stort pos back to 0
			// 	// .pause()
			// 	;

			// ------------------------------------------------ STD END


			// ---------- NEW SPLITTEXT ANIM
			var headlineManchet = [];
			if(this.$refs.headline.$refs.headline){
				headlineManchet.push( this.$refs.headline.$refs.headline );

				// COMMENT: Below code is "split-text'ing" the headline (not desired at this time)
				// mySplitText = new SplitText("#quote", {type:"words,chars"})
				// var mySplitText = new SplitText(this.$refs.headline.$refs.headline, {type:'words'});
				// console.log('mySplitText: ', typeof mySplitText.words);
				// [].forEach.call(mySplitText.words, function(word, i){
				// 	headlineManchet.push(word);
				// });
				// headlineManchet.reverse();
			}
			if(this.$refs.headline.$refs.manchet){
				headlineManchet.push( this.$refs.headline.$refs.manchet );
			}
			// console.log('headlineManchet: ', headlineManchet);
			if(headlineManchet.length > 0){
				// this.$refs.headline.$el.classList('show');
				this.introTl
					.set(this.$refs.headline.$el, {autoAlpha: 1});
				this.introTl
					.add('start')
					// ----- Linear easing
					.staggerFromTo(headlineManchet, 1, 
						{ 
							y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
						},
						{ 
							y: '-50%',
							autoAlpha: 1,
							ease: Power1.easeOut,
							// ease: Linear.easeNone,
						},
						'.1'
					)
					.add('halfway')
					.staggerTo(headlineManchet, 1,
						{ 
							y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
							// delay: .1,
							ease: Power2.easeIn,
							// ease: Linear.easeNone,
						},
						'.1',
						'halfway+=0'
					)
					.add('end')
					.progress(1) // caching animation
					.progress(0) // set stort pos back to 0
					// .pause()
					;
			} else {
				// COMMENT: This "else" is for the scene BrandedVideo which has no headline or manchet, but the "headline-wrapper"
				// we still need this timeline to be able to control the app-frame
				this.introTl
					.add('start')
					// ----- Linear easing
					.fromTo(this.$refs.headline.$el, .5, 
						{ 
							y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
						},
						{ 
							y: '-50%',
							autoAlpha: 1,
							ease: Power2.easeIn,
							// ease: Linear.easeNone,
						}
					)
					.add('50')
					.to(this.$refs.headline.$el, .5,
						{ 
							y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
							delay: .2,
							ease: Power2.easeIn,
							// ease: Linear.easeNone,
						}
					)
					.add('end')
					.progress(1) // caching animation
					.progress(0) // set stort pos back to 0
					// .pause()
					;
			}
			// ----------------------------------------------- NEW SPLITTEXT END

			// if branded-video hero, control visibility of the app-frame
			if(this.options.name == 'branded-video'){
				this.introTl.addCallback(() => {
					// console.log('scrollDirection: ', scrollDirection);
					if(scrollDirection == 'down'){
						this.$store.commit({
							type: 'showMenu',
							hide: true
						});
					} else {
						this.$store.commit({
							type: 'showMenu',
							hide: false
						});
					}
				}, 'start+=0.1');
				this.introTl.addCallback(() => {
					if(scrollDirection == 'down'){
						this.$store.commit({
							type: 'showMenu',
							hide: false
						});
					} else {
						this.$store.commit({
							type: 'showMenu',
							hide: true
						});
					}
				}, 'end-=0.1');
			}
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene-hero {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: calc(100% + 63px);
	color: white;

	.video-click {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		opacity: 1;
		// background-color: $color-carousel-pink;

		z-index: 20;
		// background-color: rgba(255, 0, 0, 0.5);
	}

	.scene-headline {
		opacity: 0;
		visibility: hidden;

		&.show {
			opacity: 1;
			visibility: visible;
		}

		h1,
		.scene-headline__manchet {
			position: absolute;
			opacity: 0;
			visibility: hidden;
		}
	}

	.scroll-indicator {
		.arrow-wrapper {
			position: fixed;
			bottom: rem(30);
			left: 50%;
			transform: translateX(-50%);

			width: rem(60);
			height: rem(60);

			@include mq($from: screen-md){
				width: rem(85);
				height: rem(85);
				bottom: rem(60);
			}
			@include mq($from: screen-xl){
				width: rem(120);
				height: rem(120);
				bottom: rem(80);
			}

			.arrow-circle {
				background-color: rgba(51, 51, 51, 0.3); // #333
				border-radius: 50%;

				width: 100%;
				height: 100%;

				opacity: 0;
				visibility: hidden;
			}

			.arrow-down {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);

				opacity: 0;
				visibility: hidden;

				@include mq($until: screen-md){
					width: rem(13);
					height: rem(20);
				}
				@include mq($from: screen-md, $until: screen-xl){
					width: rem(18);
					height: rem(30);
				}
				// @include mq($from: screen-xl){
					// ori = 22 x 36px
				// }
			}
		}
	}
}

</style>



