<template>
	<section class="scene-content" :class="[className, inView ? 'active-pointer' : '']">
		<div class="bg" ref="bg"></div>
		<LadybirdSmallAnimation
			ref="ladybird1"
			id="ladybird1"
			position="ladybird1"
			width=100
			height=100
			v-on:animationCompleted="ladybirdAnimationCompleted"
			:sequenceDelay="1900"
			v-if="mq.md"
		/>
		<!-- <LadybirdSmallAnimation
			ref="ladybird2"
			id="ladybird2"
			position="ladybird2"
			width=100
			height=100
			:sequenceDelay="2800"
			v-if="mq.md"
		/> -->
		<FlowerTwoLeaves
			ref="FlowerTwoLeaves"
			id="flower-twoleaves"
			position="flower-twoleaves--ladybird"
			width=400
			height=500
			v-if="mq.md"
			/>

		<div class="feed-wrapper" ref="feed">
			<InstagramFeed :feed="instagramFeed" :token="instagramToken" />
		</div>

		<div class="content" ref="content">
			<div class="ladybird-fixed">
				<img :src="`${staticRoot}images/ladybird/ladybird-trim.png`" alt="">
			</div>
			<div class="text">
				{{ $t('ladybird_content_headline') }}
				<div class="skinny">{{ $t('pandoragarden_hashtag') }}</div>
			</div>
		</div>
	</section>
</template>

<script>
import { MapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';
import InstagramFeed from '../InstagramFeed';
import LadybirdSmallAnimation from '../animations/LadybirdSmall';
import FlowerTwoLeaves from '../animations/FlowerTwoLeaves';

export default {
	name: 'SceneContentLadybird',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
		InstagramFeed,
		LadybirdSmallAnimation,
		FlowerTwoLeaves,
	},
	props: {
		options: Object,
		className: String,
	},
	// mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		// this.$nextTick(this.setupScene);
	// },
	data() {
		return {
			staticRoot: this.$store.state.staticRoot,
			mq: this.$store.state.mq,
			instagramFeed: this.$store.state.instagram.feed,
			instagramToken: this.$store.state.instagram.token,
		};
	},
	methods: {
		ladybirdAnimationCompleted() {
			// console.log('ladybird animation completed');
		},
		instagramFeedLoaded() {
			this.$nextTick(this.setupScene);
		},
		setupScene(){
			// var bg = document.querySelector('.bg--main'),
			var bg = this.$refs.bg,
				content = this.$refs.content,
				feed = this.$refs.feed,
				feedChildren = this.$refs.feed.getElementsByTagName('li'),
				ladybird = this.$refs.content.getElementsByClassName('ladybird-fixed')[0],
				text = this.$refs.content.getElementsByClassName('text')[0],
				feedHeight = feed.clientHeight,
				tlProgress = 0,
				scrollDirection = 'down',
				feedEaseMobile = SlowMo.ease.config(0.55, 0.4, false),
				feedEaseDesktop = CustomEase.create("custom", "M0,0,C0.078,0.44,0.21,0.474,0.452,0.548,0.701,0.624,0.788,0.638,0.906,0.756,0.952,0.817,0.982,0.887,1,1"),
				feedEase;

			if (window.matchMedia("(min-width: 1025px)").matches) {
				feedEase = feedEaseDesktop;
			} else {
				feedEase = feedEaseMobile;
			}

			this.introTl = new TimelineMax({paused: true, onUpdateParams: ['{self}'], onUpdate: function(introTl) {
				var currentTlProgress = introTl.progress();﻿
				if(currentTlProgress > tlProgress){
					scrollDirection = 'down'
				} else {
					scrollDirection = 'up'
				}
				tlProgress = introTl.progress();﻿
			}});

			// this.introTl
			// 	.set(bg, {autoAlpha: 0});

			this.introTl
				.add('intro-start')
				.fromTo(bg, 1,
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.add('content-start')

				.addLabel('ladybird1-intro', 'content-start-=0.3') // timing cannot be 0, as it doesn't provoke the direction
				.addLabel('ladybird2-intro', 'content-start-=0.1')
				.addLabel('twoleaves-intro', 'content-start-=0.1')
				// .addLabel('ladybird3-intro', 'content-start-=0.1')

				.to(feed, 0, {
					autoAlpha: 1
				})

				.fromTo(content, 2,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: this.$store.state.windowHeight,
						autoAlpha: 0,
						// delay: -20,
					},
					{
						y: '1   0%',
						autoAlpha: 1,
						ease: Linear.easeNone,
						delay: 4,
					}, '-=5.5'
				)

				.fromTo(text, 3,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: '300%',
						delay: -4,
					},
					{
						y: '0%',
						ease: Power4.easeOut,
						// delay: -4,
					}
				)

				.staggerFromTo(feedChildren, 2,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: this.$store.state.windowHeight,
						autoAlpha: 1,
						delay: -5,
					},
					{
						y: 0 - (feedHeight * 1.1),
						autoAlpha: 1,
						ease: feedEase,
					}, .03, '-=2.9'
				)

				.add('content-outro')				
				.addLabel('ladybird1-outro', 'content-outro-=0.6') // timing cannot be 0, as it doesn't provoke the direction
				.addLabel('ladybird2-outro', 'content-outro-=0.4')
				.addLabel('twoleaves-outro', 'content-outro-=0.4')
				// .addLabel('ladybird3-outro', 'content-outro-=0.2')

				.fromTo(ladybird, 1,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: '0%',
						delay: -0.7,
					},
					{
						y: '-200%',
						ease: Power4.easeOut,
						delay: -0.1,
					}, '-=0.5'
				)

				.fromTo(content, 1,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: '50%',
						autoAlpha: 1,
						delay: 0.4,
					},
					{
						y: '-=' + (this.$store.state.windowHeight + (this.$store.state.windowHeight / 100 * 30)) + 'px',
						autoAlpha: 0,
						ease: Power4.easeIn,
						delay: -1.4,
					}
				)
				.add('content-end')
				.fromTo(bg, 0.6,
					{
						autoAlpha: 1,
						delay: -1,
					},
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
						delay: -1,
					}
				)
				.add('end')
				.progress(1)
				.progress(0)
				// .pause()
				;

			if(this.mq.md){ // COMMENT: media query check, same as on the animation-components in the template
				var ladybirdAnimations = [ // corresponds to the same animation-components in the tempalte above
					{
						el: this.$refs.ladybird1,
						introLabel: 'ladybird1-intro',
						outroLabel: 'ladybird1-outro',
					},
					// {
					// 	el: this.$refs.ladybird2,
					// 	introLabel: 'ladybird2-intro',
					// 	outroLabel: 'ladybird2-outro',
					// },
					{
						el: this.$refs.FlowerTwoLeaves,
						introLabel: 'twoleaves-intro',
						outroLabel: 'twoleaves-outro',
					},
					// {
					// 	el: this.$refs.ladybird3,
					// 	introLabel: 'ladybird3-intro',
					// 	outroLabel: 'ladybird3-outro',
					// },
				];

				ladybirdAnimations.forEach(function(ladybird, i){
					// trigger ladybird intro animations based on gsap introTl timeline labels
					this.introTl.addCallback(() => {
						// console.log('ladybird this: ', this);
						if(scrollDirection == 'down'){
							ladybird.el.setForward();
							ladybird.el.play();
						} else {
							ladybird.el.playOutro();
						}
					}, ladybird.introLabel);

					// // trigger ladybird outro animations based on gsap introTl timeline labels
					this.introTl.addCallback(() => {
						// console.log('ladybird this: ', this);
						if(scrollDirection == 'down'){
							ladybird.el.playOutro();
						} else {
							ladybird.el.setForward();
							ladybird.el.play();
						}
					}, ladybird.outroLabel);
				}.bind(this));
			}
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene-content {
	opacity: 1;

	.bg {
		z-index: 0;
	}

	.scene-headline { // h2
		position: relative;
		font-size: rem(60);
		top: 4vw;
		left: 50%;
		transform: translateX(-50%);
		text-align: center;
	}

	.content {
		position: absolute;
		top: 10%;
		left: 30%;
		opacity: 0;
		visibility: hidden;
		width: 68%;
		z-index: 10;

		pointer-events: none;
		@include mq($from: screen-md){
			left: 49%;
			width: 50%;
		}

		.text {
			width: 90%;
			z-index: 2;
		
			font-family: $font-stack-bold;
			font-size: rem(50);
			text-align: left;
			line-height: 1.1;


			@include mq($from: screen-md){
				width: 60%;
				font-size: rem(80);
			}
		}

		.skinny {
			font-weight: normal;
		}

		.ladybird-fixed { // the ladybird image
			display: none;
			position: relative;
			float: right;
			width: 40%;
			max-height: rem(600);
			left: -3%;

			z-index: 1;

			@include mq($from: screen-md){
				display: block;
			}

			img {
				display: block;
				max-width: 100%;
				max-height: 100%;
				transform: scale(1.2);
			}

			// img {
			// 	position: absolute;
			// 	top: 30%;
			// 	left: 50%;
			// 	transform: translate(-50%, -50%);
			// 	width: 280%;
			// }
		}
	}
	.ladybird1,
	.ladybird2,
	.ladybird3 {
		z-index: 0;
		position: absolute;
	}
	.ladybird1 {
		transform: rotate(20deg);
		top: 24%;
		left: rem(30);

		width: rem(200); // width is, for some reason, double the height
		height: rem(100);
	}
	.ladybird2 {
		transform: rotate(10deg);
		top: 18%;
		left: 80%;

		width: rem(200);
		height: rem(100);
	}
	// .ladybird3 {
	// 	transform: rotate(-15deg);
	// 	top: 77%;
	// 	left: 70%;

	// 	width: rem(320);
	// 	height: rem(160);
	// }

	.feed-wrapper {
		position: absolute;
		top: 0;
		
		opacity: 0;
		visibility: hidden;
		width: 100%;
		padding-top: rem(50);

		@include mq($from: screen-md){
			right: 34%;
			max-width: 60%;
		}

		@include mq($from: screen-lg){
			right: 50%;
			width: rem(720);
			max-width: 48%;
		}
	}

	.flower.flower-twoleaves.flower-twoleaves--top-left {
		z-index: 0;
	}
}
</style>



