<template>
	<header :class="[isHidden ? 'site-header is-hidden' : 'site-header']">
		<div class="site-header__inner">
			<h1 class="site-header__logo">
				<a href="javascript:;" @click.prevent="gotoGlobalPandora">
					<svg viewBox="0 0 491.347 99.52" class="icon">
						<use xlink:href="#logo"></use>
					</svg>
					<span class="visually-hidden">{{$t('general_title')}}</span>
				</a>
			</h1>

			<nav :class="[showLanguageNav ? 'site-header__language-nav is-active' : 'site-header__language-nav']">
				<button type="button" v-on:click="showLanguageNav = !showLanguageNav; showProducts = false">
					<span class="site-header__language-nav__flag">
						<!-- <img :src="selectedCountryIcon" :alt="selectedCountry"> -->
						<svg viewBox="0 0 100 100" class="icon">
							<use :xlink:href="`#${selectedCountryIcon}`"></use>
						</svg>
					</span>
				</button>

				<div :class="[showLanguageNav ? 'slide-in-modal slide-in-modal--auto-width slide-in-modal--from-left is-active' : 'slide-in-modal slide-in-modal--auto-width slide-in-modal--from-left']">
					<div class="slide-in-modal__inner">
						<ul>
							<li v-for="country in countries" :key="country.countryCode">
								<a href="javascript:;" v-on:click="onCountryChange(country.countryCode, $event)">
									<span class="site-header__language-nav__flag">
										<!-- <img :src="country.icon" :alt="country.name"> -->
										<svg viewBox="0 0 100 100" class="icon" :data-cc="country.countryCode">
											<use :xlink:href="`#${country.countryCode.slice(3,5).toLowerCase()}`"></use>
										</svg>
									</span>{{ country.name }}</a>
							</li>
						</ul>
					</div>

					<span class="slide-in-modal__outside-click" v-on:click="showLanguageNav = !showLanguageNav; showProducts = false"></span>
				</div>
			</nav>

			<nav :class="[showProducts ? 'site-header__products is-active' : 'site-header__products']">
				<button type="button" class="site-header__products__trigger" v-on:click="showProducts = !showProducts; showLanguageNav = false">
					<span class="site-header__products__trigger-text">{{ $t('menu_cta_text') }}</span>

					<span class="site-header__products__trigger-dots">
						<span></span><span></span><span></span>
					</span>
				</button>

				<div :class="[showProducts ? 'slide-in-modal is-active' : 'slide-in-modal']">
					<div class="slide-in-modal__inner text--center">
						<h2 class="slide-in-modal__headline">{{ $t('menu_headline') }}</h2>
						<p class="slide-in-modal__text">{{ $t('menu_manchet') }}</p>

						<ul class="products">
							<li v-for="product in products" :key="product.sku">
								<button type="button" class="custom-cursor" @click="openClosedModal({sku: product.sku, isCard: false, target: $event.target})">
									<img :src="product.images.packshot">
								</button>
							</li>
						</ul>

						<a href="#" class="button">{{ $t('endscene_cta') }}</a>
					</div>

					<span class="slide-in-modal__outside-click" v-on:click="showProducts = !showProducts; showLanguageNav = false"></span>
				</div>
			</nav>
		</div>
		<div class="site-borders">
			<span class="site-borders__element site-borders__element--left"></span>
			<span class="site-borders__element site-borders__element--bottom"></span>
			<span class="site-borders__element site-borders__element--right"></span>
		</div>
	</header>
</template>

<!--
	COMPONENT DESCRIPTION

	At the time of writing, the two flower-animations are set to autoplay in different ways, in order to test the most performant approach..

-->

<script>
import { mapActions } from 'vuex';

// import { TweenMax, TimelineMax } from 'gsap';
import { Power4, TimelineMax } from 'gsap';

// import InstagramFeedEntry from './InstagramFeedEntry';
import Utils from './mixins/Utils';

export default {
	name: 'SiteHeader',
	components: {
		// InstagramFeedEntry,
	},
	methods: {
		...mapActions([
			'openClosedModal',
		]),
		onCountryChange(countryCode, event) {
			event.preventDefault();
			
			// alert('transfering to: ', Utils.methods.getCountryLink(countryCode, this.$store.state.basePandoraCampaignUrl));
			// TODO add tracking at this point
			//window.location = Utils.methods.getCountryLink(countryCode);
			this.$i18n.locale = countryCode;
			this.showLanguageNav = false;
		},
		gotoGlobalPandora(){
			const url = this.$store.state.countries.filter(c => c.countryCode == this.$i18n.locale && c.url)
			window.location = url.length ? url[0].url : Utils.methods.getCountryLink(this.$i18n.locale, this.$store.state.basePandoraUrl);
		}
	},
	data() {
		return {
			staticRoot: this.$store.state.staticRoot,
			mq: this.$store.state.mq,
			showProducts: false,
			showLanguageNav: false,
			countries: this.$store.state.countries,
			selectedCountry: this.$i18n.locale,
		};
	},
	computed: {
		isHidden() {
			return this.$store.state.hideSiteHeader;
		},
		selectedCountryIcon(){
			return this.$i18n.locale.slice(3,5).toLowerCase();
		},
		products() {
			return this.$store.state.products.filter(product => !product.hideInMenu)
				.sort( (a,b)=>a.menuOrder - b.menuOrder );
		}
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../assets/global';

$hideSiteHeaderTransition: transform 1.2s ease; // REMEMBER: Set the same timing in store.js (appFrameTransitionDuration)

.site-header {
	position: relative;
	z-index: 9;

	&__inner {
		display: block;
		width: 100%;
		height: rem($main-menu-height);
		padding: 0;

		background-color: $color-white;

		position: fixed;
		top: 0;
		left: 0;
		z-index: 1;

		transition: $hideSiteHeaderTransition;

		@include mq($from: screen-md) {
			height: rem($main-menu-height-md);
		}
		@include mq($from: screen-xl) {
			height: rem($main-menu-height-lg);
		}
	}

	button[type="button"] {
		cursor: pointer;
	}

	&__logo {
		display: block;
		width: rem(90);
		padding: 0;
		margin:  rem(11) 0 0;

		position: absolute;
		left: 50%;
		top: 0;
		transform: translateX(-50%);

		@include mq($from: screen-md) {
			width: rem(112);
			margin: rem(16) 0 0;
		}
		@include mq($from: screen-xl) {
			width: rem(142);
			margin: rem(23) 0 0;
		}

		img, svg {
			display: block;
			width: 100%;
		}
	}

	&__language-nav {
		ul {
			display: block;
			padding: 0 0 rem(80);
			margin: 0;

			@include mq($from: screen-md) {
				padding-bottom: 0;
			}
		}

		a {
			display: block;
			padding: rem(10) rem(70) rem(10) rem(16);
			text-decoration: none;
			color: $color-text;
			font-size: rem(18);
			white-space: nowrap;

			transition: background-color .25s ease;

			@include mq($from: screen-sm) {
				// font-size: rem(20);
			}
			@include mq($from: screen-md) {
				padding: rem(10) rem(70) rem(10) rem(29);

				// font-size: rem($menu-link-max - 6);
			}
			@include mq($from: screen-lg) {
				padding: rem(14) rem(70) rem(14) rem(29);

				// font-size: rem($menu-link-max - 4);
			}

			&:hover {
				background-color: #EBECEC;
			}

			&.is-active {
				font-family: $font-stack;
			}
		}

		button {
			position: relative;
			width: rem(46);
			height: rem(40);
			padding: 0;
			margin: 0;
			appearance: none;
			outline: none;
			border: none;
			background-color: transparent;

			transition: background-color .25s ease;

			@include mq($from: screen-md) {
				width: rem(66);
				height: rem(60);
			}
			@include mq($from: screen-xl) {
				width: rem(86);
				height: rem(80);
			}

			.site-header__language-nav__flag {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}

		&__flag {
			position: relative;
			display: inline-block;
			width: rem(15);
			height: rem(15);
			margin: 0 rem(15) 0 0;
			border-radius: 50%;
			background-color: rgba(0,0,0,.3);
			overflow: hidden;
			transform: translateY(rem(1));

			top: 0;

			@include mq($from: screen-md) {
				width: rem(20);
				height: rem(20);
				margin: 0 rem(22) 0 0;

				transform: translateY(rem(4));
			}
			@include mq($from: screen-xl) {
				width: rem(24);
				height: rem(24);
				margin: 0 rem(30) 0 0;

				transform: translateY(rem(4));
			}

			img, svg {
				width: inherit;
				height: inherit;
				position: absolute;
				top: 0;
				left: 0;

				border-radius: 50%;
			}
		}

		&.is-active {
			button {
				background-color: #F6F7F7;
			}
		}
	}

	&__products {
		position: absolute;
		top: 0;
		right: 0;

		.slide-in-modal {
			text-align: center;
		}

		&__trigger {
			display: block;
			padding: 0;
			margin: 0;
			appearance: none;
			border: none;
			outline: none;
			background-color: $color-white;

			height: rem(40);

			@include mq($from: screen-md){
				height: rem(60);
			}
			@include mq($from: screen-xl){
				height: rem(80);
			}
		}

		&__trigger-text {
			display: none;
			
			font-size: rem(15);
			text-transform: uppercase;
			font-family: $font-stack;

			transform: translate(0, rem(-5));

			transition: opacity .25s ease .1s, transform .25s ease;

			@include mq($from: screen-sm) {
				display: inline-block;
				font-size: rem(12);
				transform: translate(0, rem(-3));
			}
			@include mq($from: screen-md) {
				font-size: rem(13);
				transform: translate(0, rem(-4));
			}
			@include mq($from: screen-xl) {
				font-size: rem(15);
				transform: translate(0, rem(-5));
			}
		}

		&__trigger-dots {
			display: inline-block;
			padding: rem(4) rem(17) rem(10) rem(4);
			transition: background-color .25s ease;

			@include mq($from: screen-md) {
				padding: rem(17) rem(20);
			}
			@include mq($from: screen-xl) {
				padding: rem(27) rem(30);
			}

			span {
				display: inline-block;
				width: rem(9);
				text-align: right;

				@include mq($from: screen-md) {
					width: rem(14);
				}

				&:before,
				&:after {
					content: '';
					display: inline-block;
					width: rem(6);
					height: rem(6);
					margin-bottom: rem(-4);
					border-radius: 50%;
					background-color: $color-carousel-pink;

					transition: background-color .25s ease;

					@include mq($from: screen-md) {
						width: rem(9);
						height: rem(9);
						margin-bottom: rem(2);
					}
				}
				&:after {
					margin-bottom: 0;
				}
			}
		}

		&.is-active {
			.site-header__products__trigger-text {
				opacity: 0;
				cursor: default;
				pointer-events: none;
				transform: translate(100%, rem(-5));
				transition: opacity .25s ease, transform .3s ease .1s;
			}

			.site-header__products__trigger-dots {
				background-color: #F6F7F7;

				& span:before,
				& span:after {
					background-color: #686C6F;
				}
			}
		}

		.slide-in-modal__inner {
			padding: 0 rem(40);

			@include mq($from: screen-md) {
				padding: 0 rem(70);
			}
		}
		
	}

	&__modal-link {
		text-align: right;
	}

	&.is-hidden {
		.site-header__inner {
			transform: translateY(-100%);
		}

		.site-borders {
			&__element {
				&--left {
					transform: translateX(-100%);
				}

				&--right {
					transform: translateX(100%);
				}

				&--bottom {
					transform: translateY(100%);
				}
			}
		}

		.slide-in-modal {
			transform: translateX(100%);
			transition: transform .15s ease, box-shadow .15s ease;
		}

		.slide-in-modal--from-left {
			transform: translateX(-100%);
		}
	}
}

.slide-in-modal {
	position: fixed;
	top: rem(40);
	right: rem(0);
	z-index: -1;
	width: 100%;
	max-width: rem(1250);
	height: calc(100vh - rem(40));
	padding: 0;
	box-sizing: border-box;
	background-color: #F6F7F7;

	transform: translateX(100%);
	transition: transform .45s ease, box-shadow .35s ease;

	@include mq($from: screen-md) {
		top: rem(60);
		height: calc(100vh - rem(60));
	}
	@include mq($from: screen-lg) {
		width: 55%;
	}
	@include mq($from: screen-xl) {
		top: rem(80);
		height: calc(100vh - rem(80));
	}

	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		width: 120%;
		height: rem(40);
		background-color: $color-white;

		transform: translateY(-100%);

		@include mq($from: screen-md) {
			height: rem(60);
		}
		@include mq($from: screen-xl) {
			height: rem(80);
		}
	}

	&__inner {
		height: 100%;
		overflow-y: auto;
		overflow-x: hidden;
		-webkit-overflow-scrolling: touch;
		

		&::-webkit-scrollbar {
			width: rem(4);
		}
		&::-webkit-scrollbar-track {}
		&::-webkit-scrollbar-thumb {
			background-color: darkgrey;
			outline: 1px solid slategrey;
		}
		&.text--center {
			text-align: center;
		}
		.button {
			margin-bottom: rem(100);
			max-width: 50%;
			display: inline-block;

		}
	}

	&__headline {
		display: block;
		margin: rem(40) 0 rem(15);
		padding: 0;

		font-size: rem(34);
		font-weight: normal;
		text-align: center;
		color: $color-text;

		@include mq($from: screen-sm){
			font-size: rem(38);
			margin: rem(50) 0 rem(15);
		}
		@include mq($from: screen-md){
			font-size: rem(45);
			margin: rem(60) 0 rem(15);
		}
		@include mq($from: screen-xl){
			font-size: rem(60);
			margin: rem(80) 0 rem(15);
		}
	}

	&__text {
		display: block;
		margin: 0 0 rem(40);
		padding: 0;

		font-size: rem(18);
		text-align: center;
		color: $color-text;

		@include mq($from: screen-md){
			font-size: rem(20);
		}
		@include mq($from: screen-md){
			//font-size: rem(24);
		}
		@include mq($from: screen-xl){
			//font-size: rem(30);
		}
	}

	&--from-left {
		right: auto;
		left: 0;
		transform: translateX(-100%);

		&:before {
			right: auto;
			left: 0;
		}

		.slide-in-modal__outside-click {
			right: auto;
			left: 0;
		}
	}

	&--auto-width {
		@include mq($from: screen-md) {
			width: auto;
		}
	}

	&.is-active {
		transform: translateX(0);
		box-shadow: rem(5) 0 rem(40) 0 rgba(0,0,0,.3);

		.slide-in-modal__outside-click {
			display: block;
		}
	}

	&__outside-click {
		display: none;

		position: absolute;
		top: 0;
		right: 0;
		z-index: -2;

		width: 100vw;
		height: 100%;
	}
}

.products {
	display: flex;
	flex-flow: row wrap;
	padding: 0;
	margin: 0 0 rem(40);

	li {
		display: block;
		flex: 0 1 50%;
		width: 50%;
		margin-bottom: rem(10);
		list-style-type: none;
		@include mq($from: screen-sm){
			flex: 0 1 25%;
			width: 25%;
		}
		button,
		img {
			display: block;
			width: 100%;
		}
		button {
			border: none;
			outline: none;
			background-color: transparent;
		}
		img {
			transform: scale(1);
			transition: transform .35s ease;
		}
		button:hover img {
			transform: scale(1.1);
		}
	}
}

.site-borders {
	width: 0;
	height: 0;

	&__element {
		display: block;
		background-color: $color-white;
		position: fixed;
		z-index: 0;
		transition: $hideSiteHeaderTransition;

		&--left,
		&--right {
			width: rem(15);
			height: 100vh;
			
			@include mq($from: screen-md) {
				width: rem(20);
			}
			@include mq($from: screen-xl) {
				width: rem(30);
			}
		} 
		&--left {
			left: 0;
			top: 0;
		}
		&--right {
			right: 0;
			top: 0;
		}
		&--bottom {
			height: rem(15);
			width: 100vw;
			left: 0;
			bottom: 0;

			@include mq($from: screen-md) {
				height: rem(20);
			}
			@include mq($from: screen-xl) {
				height: rem(30);
			}
		}
	}
}
</style>



