<template>
	<section class="scene-content" :class="[className, inView ? 'active-pointer' : '']">
		<!-- <h2 class="scene-headline" ref="headline">Blossom content headline</h2> -->
		<div class="bg--half" ref="bg">
			<!-- <div class="image-wrapper"><img src="/static/images/blossom/blossom-content-background.jpg" alt=""></div> -->
		</div>

		<div class="all-texts" ref="texts">
			<div v-for="(product, index) in products" :class="['text', 'text--'+index]" :key="index">
				{{ $t(product.text) }}
			</div>
		</div>

		<div class="content content-list" ref="content">
			<div type="button" class="list-item custom-cursor" v-for="(product, index) in products" :key="index" @click="openClosedModal({sku: product.sku, target: $event.target})">
				<figure class="product-wrapper">
					<img :src="product.images.packshot" :alt="product.type" />
				</figure>
			</div>
		</div>

		<div class="visualizer"></div>
	</section>
</template>

<script>
import { mapActions } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

// import GSDevTools from 'GSDevTools';

import SceneHeadline from '../SceneHeadline';

export default {
	name: 'SceneContentBlossom',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
	},
	props: {
		options: Object,
		className: String,
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
	},
	data(){
		return {
			mq: this.$store.state.mq,
			products: this.$store.state.products.filter(product => product.type === 'blossom')
		}
	},
	methods: {
		...mapActions([
			'openClosedModal',
		]),
		setupScene(){
			// 	content = this.$refs.content;

			this.introTl = new TimelineMax({id: 'blossom-content', paused: true}); // TMP - rename timeline for gsap debug

			// var bgMain = document.querySelector('.bg--main'); // set main bg to hidden
			// this.introTl
			// 	.set(bgMain, {autoAlpha: 0});

			var listItems = this.$refs.content.querySelectorAll('.list-item');
			var listItemsCount = listItems.length;

			// var texts = document.querySelectorAll('.all-texts .text');
			var texts = this.$refs.texts.querySelectorAll('.text');

			// var listItems = do;
			var content = this.$refs.content,
				contentHeight = content.clientHeight;

			// console.log('contentHeight: ', contentHeight);

			var bg = this.$refs.bg;

			var contentDuration = 100;
			var vh = this.$store.state.windowHeight;

			// // var itemEasingIn = CustomEase.create('itemEasingIn', 'M0,0 C0.456,0.456 0.713,0.842 0.774,0.902 0.874,1 0.942,1 1,1');
			// // var itemEasingIn = CustomEase.create('custom', 'M0,0 C0.51,0.348 0.556,0.306 0.718,0.642 0.899,1.018 0.948,1 1,1');
			// var itemEasingIn = CustomEase.create("custom", "M0,0,C0.32,0.38,0.556,0.561,0.646,0.716,0.81,1,0.84,1,1,1");
			// var itemEasingIn = CustomEase.create("custom", "M0,0 C0.112,0 0.112,0.05 0.192,0.148 0.197,0.154 0.679,0.67 1,1");
			// // var itemEasingOut = CustomEase.create('custom', 'M0,0 C0.112,0 0.15,0.076 0.3,0.43 0.462,0.814 0.922,0.952 1,1');
			// var itemEasingOut = CustomEase.create("custom", "M0,0 C0.112,0 0.298,0.002 0.378,0.1 0.414,0.144 0.606,0.464 1,1");

			this.introTl
				.set(content, {autoAlpha: 1}); // make sure content is shown before animating it's insides

			// function itemAnimStart(i){
			// 	console.log('animation started i = ', this);
			// }
			// function itemAnimComplete(i){
			// 	console.log('animation complete i = ', this);
			// }

			this.introTl
				.add('start')
				.fromTo(bg, (this.$store.state.windowHeight * 0.05), // 5% of timeline
					{
						y: (this.$store.state.windowHeight / 5) + 'px', // 20 vh travel distance
						autoAlpha: 0,
					},
					{
						y: 0,
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.add('contentStart', 'start+=' + (this.$store.state.windowHeight * 0.01)) // content start point, referenced by the bubble's

				// new APPROACH
				.fromTo(content, this.$store.state.windowHeight,
					{
						y: this.$store.state.windowHeight + 'px',
					},
					{
						y: '-' + (contentHeight + 300) + 'px',
						ease: Linear.easeNone,
					}
				)
				;


			var introStartOffset = 0;
			// console.log('contentDuration: ', contentDuration);

			var itemDuration = contentDuration / listItemsCount;
			// console.log('itemDuration: ', itemDuration);

			// var introOutroDuration = itemDuration / 2; // when only intro and outro
			var introOutroDuration = itemDuration / 4; // when intro, scaleup, scaledown, outro
			// console.log('introOutroDuration: ', introOutroDuration);

			var newIntroOutroDuration = itemDuration / 3;
			// var newScaleDuration = newIntroOutroDuration / 7.3;
			var newScaleDuration = newIntroOutroDuration / 6;

			// staggerDelay is the distance between bubbles
			var itemStaggerDelay = 1.7; // should be less and less, the more bubbles in the scene

			// var itemYAnimationSteps = [
			// 	'+' + (vh / 1.2) + 'px', // start position (in regard to window)
			// 	'-1%', // start scale and y position in regard to self(translateY)
			// 	'-50%', // absolute center
			// 	'-99%',
			// 	'-' + (vh / 1.2	) + 'px'
			// ];
			var itemYAnimationSteps = [
				'+' + (vh / 1) + 'px', // start position (in regard to window)
				'+20%', // start scale and y position in regard to self(translateY)
				'-50%', // absolute center
				'-118%',
				'-' + (vh / 1.5) + 'px'
			];

			// var visualizerElem = document.querySelector('.visualizer');

			var nyStart = (this.$store.state.windowHeight * 0.21); // xxxxxl
			if(this.mq.lg){
				nyStart = (this.$store.state.windowHeight * 0.20); // 
			}
			if(this.mq.xxl){
				nyStart = (this.$store.state.windowHeight * 0.23); // 
			}
			if(this.mq.xxxl){
				nyStart = (this.$store.state.windowHeight * 0.21); // 
			}

			[].forEach.call(listItems, function(item, i){
				var itemRelatedTextElem = texts[i];

				// var textTl = new TimelineMax({paused: true});
				// textTl.fromTo

				// var itemOutroDelay = 0.2;
				var itemOutroDelay = .1; // 2 = 2%

				// // item animation timings
				// var itemIntroPosition = introStartOffset; // inherit the offset from previous items
				// var itemScaleUpPosition = (introStartOffset + (introOutroDuration * 2));
				// var itemScaleDownPosition = (introStartOffset + (introOutroDuration * 3) + itemOutroDelay);
				// var itemOutroPosition = (introStartOffset + (introOutroDuration * 4) + itemOutroDelay);

				// item animation timings
				var itemIntroPosition = introStartOffset; // inherit the offset from previous items
				var itemScaleUpPosition = (introStartOffset + newIntroOutroDuration);
				var itemScaleDownPosition = (introStartOffset + (newIntroOutroDuration + newScaleDuration) + itemOutroDelay);
				var itemOutroPosition = (introStartOffset + ((newIntroOutroDuration) + (newScaleDuration * 2)) + itemOutroDelay);
				// console.log('itemIntroPosition: ', itemIntroPosition);
				// console.log('itemScaleUpPosition: ', itemScaleUpPosition);
				// console.log('itemScaleDownPosition: ', itemScaleDownPosition);
				// console.log('itemOutroPosition: ', itemOutroPosition);


				// old code goes here!! ------------->


				var itemTl = new TimelineMax({ id: 'itemtl-' + i, paused: false }); // nested timelines inherit master pause state
				var itemTextTl = new TimelineMax({ id: 'itemTexttl-' + i, paused: false }); // nested timelines inherit master pause state
				itemTl
					.set(item, {
						autoAlpha: 1,
						// scale: 0.5,
						width: '150px',
						height: '150px',
						// transformOrigin: '100% 50%',
					})
					// .to(item, 5,

					// 	// {
					// 	// 	y: this.$store.state.windowHeight + 'px',
					// 	// },
					// 	{
					// 		y: '-' + this.$store.state.windowHeight + 'px',
					// 		autoAlpha: 1,
					// 		// ease: itemEasingIn,
					// 		// ease: Power1.easeInOut,
					// 		ease: Linear.easeNone,
					// 		// ease: Power1.easeOut,
					// 	},
					// 	// 'contentStart+=' + itemIntroPosition
					// )
					.to(item, (this.$store.state.windowHeight * 0.1),
						{
							// scale: 1,
							width: '300px',
							height: '300px',
							ease: Linear.easeNone,
						},
						// '-4'
					)
					.to(item, (this.$store.state.windowHeight * 0.1),
						{
							// scale: 0.5,
							width: '150px',
							height: '150px',
							ease: Linear.easeNone,
						},
						// '-2'
					)
					;
				itemTextTl
					.set(itemRelatedTextElem, {
						autoAlpha: 0,
						// transformOrigin: '100% 50%',
					})
					// .to(item, 5,

					// 	// {
					// 	// 	y: this.$store.state.windowHeight + 'px',
					// 	// },
					// 	{
					// 		y: '-' + this.$store.state.windowHeight + 'px',
					// 		autoAlpha: 1,
					// 		// ease: itemEasingIn,
					// 		// ease: Power1.easeInOut,
					// 		ease: Linear.easeNone,
					// 		// ease: Power1.easeOut,
					// 	},
					// 	// 'contentStart+=' + itemIntroPosition
					// )
					.fromTo(itemRelatedTextElem, (this.$store.state.windowHeight * 0.03),
						{
							y: '-45%',
							autoAlpha: 0,
						},
						{
							y: '-50%',
							autoAlpha: 1,
							// ease: itemEasingIn,
							// ease: Power1.easeInOut,
							ease: Linear.easeNone,
						},
						// '-4'
					)
					.fromTo(itemRelatedTextElem, (this.$store.state.windowHeight * 0.03),
						{
							y: '-50%',
							autoAlpha: 1,
						},
						{
							y: '-55%',
							autoAlpha: 0,
							// ease: itemEasingIn,
							// ease: Power1.easeInOut,
							ease: Linear.easeNone,
							delay: (this.$store.state.windowHeight * 0.08), // COMMENT: This delay determines how long the text is at full visibility
						},
						// '-2'
					)
					;

				var itemStart = (nyStart + ((this.$store.state.windowHeight * .09) * i));
				var itemTextStart = (nyStart + ((this.$store.state.windowHeight * .11) * i));
				if(this.mq.lg){
					itemStart = (nyStart + ((this.$store.state.windowHeight * .09) * i));
					itemTextStart = (nyStart + ((this.$store.state.windowHeight * .14) * i));
				}
				if(this.mq.xxl){
					itemStart = (nyStart + ((this.$store.state.windowHeight * .08) * i));
					itemTextStart = (nyStart + ((this.$store.state.windowHeight * .11) * i));
				}
				this.introTl
					.add(itemTl, 'contentStart+=' + itemStart)
					.add(itemTextTl, 'contentStart+=' + itemTextStart)
					;


				// this.introTl.timeScale(0.5);

				// visualizer setup
				// var visualizerDur = document.createElement('div');
				// visualizerDur.innerHTML = 'dur';
				// visualizerDur.classList.add('dur');
				// // visualizerDur.style.width = ((introOutroDuration * 2) * 10) + 'px';
				// visualizerDur.style.width = ((introOutroDuration ) * 10) + 'px';
				// visualizerDur.style.borderRightWidth = ((introOutroDuration ) * 10) + 'px';
				// visualizerDur.style.marginLeft = (introStartOffset * 10) + 'px';

				// visualizerElem.append(visualizerDur);

				introStartOffset = introStartOffset + itemStaggerDelay;
			}.bind(this));


			this.introTl
				.add('content-end')
				// .fromTo(bg, (contentDuration / 10),
				.to(bg, (this.$store.state.windowHeight * 0.05), // 5% of timeline
					// {
					// 	width: '50%', // changed from "fromTo" to "to" as for mobile is wider than 50% to start with (and media query is obsolete)
					// 	autoAlpha: 1,
					// 	// delay: -6
					// },
					{
						width: '100%',
						autoAlpha: 1,
						ease: Linear.easeNone,
						// delay: -7
						delay: -(this.$store.state.windowHeight * 0.25), // 15% of duration
					}
				)
				.to(bg, (this.$store.state.windowHeight * 0.04),
					// {
					// 	// width: '50%',
					// 	autoAlpha: 1,
					// 	// delay: -1
					// },
					{
						// width: '100%',
						autoAlpha: 0,
						ease: Linear.easeNone,
						// delay: (this.$store.state.windowHeight * 0.02),
					},
					'-=' + (this.$store.state.windowHeight * 0.20)
				)
				.add('end')
				.progress(1)
				.progress(0)
				// .pause()
				;

			// GSAP Debug setup
			// GSDevTools.create({
			// 	// id: 'blossom content',
			// 	animation: this.introTl,
			// 	globalSync: false,
			// 	hideGlobalTimeline: true,
			// 	paused: true,
			// });

			// console.log('blossom introTl: ', this.introTl);
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene-content {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100vh;
	color: $color-white;
	opacity: 1;

	.content {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		visibility: hidden;
		z-index: 3;
		
		// padding-left: 25%;
		padding-left: 50%;

		@include mq($from: screen-sm){
			padding-left: 0;
		}
	}
	.all-texts {
		position: absolute;
		top: 50%;
		right: 50%;
		// left: rem(40);
		z-index: 4;
		max-width: rem(350);
		text-align: right;

		@include mq($from: screen-sm){
			right: 65%;
		}

		.text {
			position: absolute;
			top: 0;
			// left: 0;
			right: 0vw;
			opacity: 0;
			visibility: hidden;

			transform: translateY(-50%);

			font-size: rem(18);
			font-weight: normal;
			color: $color-text;
			line-height: 2.4;

			width: 45vw;

			@include mq($from: screen-sm){
				width: 32vw;
				font-size: rem(24);
			}
			@include mq($from: screen-md){
				width: rem(360);
				font-size: rem(28);
			}
		}
		.cta { // a-tag
			position: relative;
			display: block;
			color: $color-white;
			margin-top: rem(20);
		}
	}
	.list-item {
		// position: absolute;
		// top: 50%; // old value
		// // top: 100%;
		// left: 0;
		// width: 100%;
		text-align: center;

		// transform: translateY(-50%) scale(1.5);

		opacity: 1;
		visibility: visible;

		margin: 0 auto rem(50);

		// transform: scale(0.5);
		width: rem(150);
		height: rem(150);

		.product-wrapper {
			position: relative;
			margin: auto;
			background-color: $color-white;
			border-radius: 50%;
			// width: rem(250);
			// height: rem(250);
			width: 100%;
			height: 100%;

			overflow: hidden;

			img {
				position: absolute;
				top: 0;
				left: 0;
				// transform: translate(-57%, -25%);
				width: 100%;
			}

			// margin-bottom: rem(240);
		}

		// border: rem(1) solid white;
		// &:nth-child(1) {
		// 	border-color: red;
		// }
		// &:nth-child(2) {
		// 	border-color: green;
		// }
		// &:nth-child(3) {
		// 	border-color: blue;
		// }
	}

	.bg--half {
		position: fixed;
		top: 0;
		left: 0;
		// width: 100%;
		height: 100vh;
		opacity: 1;
		background-color: $color-carousel-pink;

		z-index: 2;

		pointer-events: none;
		user-select: none;

		width: 75%;

		@include mq($from: screen-sm){
			width: 50%;
		}

		// .image-wrapper {
		// 	position: absolute;
		// 	top: 0;
		// 	right: 0;
		// 	height: 100%;
		// 	width: 50%;
		// 	overflow: hidden;

		// 	img {
		// 		object-fit: cover;
		// 		object-position: 50% 50%;
		// 	}
		// }
	}
}

</style>



