<template>
	<div class="modal-overlay" @click="closeOpenModal" ref="overlay" :class="[hasAnimated ? 'animated' : '']">
		<div class="modal" tabindex="0" v-on:keyup.esc="closeOpenModal" @click.stop ref="modal" :class="[hasLoaded ? 'loaded' : '']">
			<div class="modal-closeButton" @click="closeOpenModal"></div>
			<picture v-if="modal.isCard" class="card-modal-image">
				<source media="(max-width: 566px)" :srcset="modal.content.images.cardSmall">
				<source media="(min-width: 567px)" :srcset="modal.content.images.cardLarge">
				<img :src="modal.content.images.cardLarge" @load="loaded">
			</picture>
			<picture v-if="!modal.isCard">
				<source media="(max-width: 566px)" :srcset="modal.content.images.lifestyleSmall">
				<source media="(min-width: 567px)" :srcset="modal.content.images.lifestyleLarge">
				<img :src="modal.content.images.lifestyleLarge" @load="loaded">
			</picture>
			<picture v-if="!modal.isCard">
				<img :src="modal.content.images.packshotLarge" @load="loaded" :srcset="`${modal.content.images.packshot} 500w`">
			</picture>
			<div class="modal-content">
				<div class="modal-price" v-if="!modal.isCard">{{ $t(`${modal.content.sku}_price`) }}</div>
				<a :href="modal.content.link" class="modal-link button">{{ $t('general_shop_now') }}</a>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapState, mapActions } from 'vuex';
	import { TweenMax, Power4 } from 'gsap';

	export default {
		name: 'Modal',
		computed: mapState([
			'modal',
		]),
		data(){
			return {
				mq: this.$store.state.mq,
				numberLoaded: 0,
				// startCoords: {},
				hasLoaded: false,
				hasAnimated: false,
			};
		},
		mounted() {
			this.position = this.$store.state.modal.position;
			// this.startCoords = this.modal.coords;
		},
		methods: {
			...mapActions([
				'closeOpenModal',
			]),
			openModal() {
				const modalCon = this.$refs.modal;
				TweenMax.set(modalCon, {
					height: 'auto',
					left: '50%',
					top: '50%',
					width: '90%',
					xPercent: -50,
					yPercent: -50,
					opacity: 1,
				});
				TweenMax.from(modalCon, 1.2, {
					// position: 'absolute',
					left: this.modal.coords.left,
					top: this.modal.coords.top,
					width: this.modal.coords.width,
					height: this.modal.coords.height,
					xPercent: 0,
					yPercent: 0,
					ease: Power4.easeOut,
					onComplete: () => {
						this.hasAnimated = true;
					},
				});
			},
			openMobile() {
				const modalCon = this.$refs.modal;
				TweenMax.set(modalCon, {
					height: '100%',
					left: '50%',
					top: '50%',
					width: '100%',
					xPercent: -50,
					yPercent: -50,
					opacity: 1,
				});
				TweenMax.from(modalCon, 0.8, {
					// position: 'absolute',
					left: this.modal.coords.left,
					top: this.modal.coords.top,
					width: this.modal.coords.width,
					height: this.modal.coords.height,
					xPercent: 0,
					yPercent: 0,
					ease: Power4.easeOut,
					onComplete: () => {
						this.hasAnimated = true;
					},
				});
			},
			loaded() {
				this.numberLoaded += 1;
				if (this.modal.isCard || this.numberLoaded > 1) {
					this.hasLoaded = true;
					window.innerWidth >= 567 ? this.openModal() : this.openMobile();
				}
			},
		},
	};
</script>

<style lang="scss" scoped>
	@import '../assets/global';

	.modal-overlay {
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		background: rgba(169, 115, 115, 0);
		transition: all 1s ease;
		z-index: 9999;

		&.animated {
			background: rgba(0, 0, 0, .6);
		}
	}

	.modal {
		position: absolute;
		top: 50%;
		left: 50%;
		display: flex;
		align-items: stretch;
		width: 90%;
		height: auto;
		max-width: rem(1500);

		background-color: $color-white;
		opacity: 0;

		picture {
			flex: 0 0 50%;

			&.card-modal-image {
				display: block;
				flex: 0 0 100%;
			}

			img {
				display: block;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		@include mq($until: screen-xs) {
			flex-wrap: wrap;
			align-items: flex-start;
			align-content: flex-start;

			picture {
				flex: 0 0 100%;
				height: 40vh;

				&:nth-of-type(2) {
					height: 40vh;

					img {
						object-fit: contain;
					}
				}
				&.card-modal-image {
					height: 100%;
					display: block;
				}
			}
		}
	}

	.modal-closeButton {
		position: absolute;
		top: rem(30);
		right: rem(30);
		height: rem(40);
		width: rem(40);

		background-color: $color-white;
		border-radius: 50%;
		box-shadow: rem(10) rem(10) rem(40) rgba(0, 0, 0, .1);
		transition: opacity 1s ease;
		opacity: 0;
		cursor: pointer;

		.animated & {
			opacity: 1;
		}

		&:before, &:after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			display: block;
			height: rem(2);
			width: rem(25);
			transform: translate(-50%) rotate(45deg);

			border-radius: rem(2);
			background-color: black;
		}

		&:after {
			transform: translate(-50%) rotate(-45deg);
		}
	}

	.modal-content {
		position: absolute;
		// box-sizing: border-box;
		right: 0;
		bottom: 0;
		padding: rem(30);
		opacity: 0;
		transition: opacity 1s ease;

		@include mq($until: screen-xs) {
			width: 100%;
		}

		.animated & {
			opacity: 1;
		}

		.button {
			background-color: #fff;
		}

		picture:focus,
		img:focus {
			outline: 0;
		}
	}

	.modal-price {
		color: $color-nevada;
		font-size: rem(30);
		font-family: $font-stack;
		letter-spacing: rem(2);
		text-align: right;
		margin-bottom: rem(20);

		@include mq($until: screen-xs) {
			text-align: center;
			font-size: rem(20);
			margin-bottom: rem(15);
		}
	}
</style>



