<template>
	<footer class="scene-content" :class="[className, inView ? 'active-pointer' : '']">
		<div class="bg" ref="bg"></div>
		<div class="content" ref="content">
			<div class="content__inner">
				<h2>{{ $t('endscene_headline') }}</h2>

				<picture ref="picture" class="poster-image" style="visibility: inherit; opacity: 1;">
					<source :srcset="`${staticRoot}images/footer-bg-sm.jpg`" media="(max-width: 768px)">
					<source :srcset="`${staticRoot}images/footer-bg-lg.jpg`" media="(min-width: 769px)">
					<img :src="`${staticRoot}images/footer-bg-lg.jpg`">
				</picture>

				<a :href="$t('endscene_cta_link')" class="button">
					{{ $t('endscene_cta') }}
				</a>
			</div>
		</div>
	</footer>
</template>

<script>
import { MapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';

export default {
	name: 'SceneContent',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
	},
	props: {
		options: Object,
		className: String,
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
	},
	data() {
	  return {
		  staticRoot: this.$store.state.staticRoot
	  };
	},
	methods: {
		setupScene(){
			// const bg = document.querySelector('.bg--main');
			const bg = this.$refs.bg;
			const content = this.$refs.content;
			const picture = this.$refs.picture;

			this.introTl
				.set(bg, { autoAlpha: 0 });

			this.introTl
				.add('intro-start')
				.to(bg, 1, {
					autoAlpha: 1,
					ease: Linear.easeNone,
				})
				.add('content-start')
				.fromTo(content, 1,
					{
						y: '30px',
						autoAlpha: 0,
						// ease: Power4.easeIn
					},
					{
						y: 0,
						autoAlpha: 1,
						ease: Power4.easeIn
					},
					'-=0.5'
				)

				.fromTo(picture, 1,
					{
						y: '70%'
					},
					{
						y: '0%'
					},
					'-=0.7'
				)

				.add('content-end')
				.add('end')
				.progress(1)
				.progress(0)
				;
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene-content {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100vh;
	color: $color-white;
	// background-color: $color-carousel-pink;
	opacity: 1;

	text-align: center;

	// pointer-events: auto;

	.bg {
		z-index: 0; // 
	}

	.content {
		width: 100%;
		height: 100vh;
		margin: 0 auto;

		opacity: 0;
		// visibility: visible;
		visibility: hidden;

		&__inner {
			width: 100%;

			position: absolute;
			top: 50%;
			transform: translateY(-50%);
		}
	}

	h2 {
		margin-bottom: rem(-10);
		margin-top: rem(10);
		text-align: center;
		font-size: rem(30);
		max-width: rem(600);
		

		opacity: 1;
		visibility: visible;
		// visibility: hidden;

		// transform: translateY(260%);

		@include mq($from: screen-sm){
			margin: 0 auto rem(0);
			font-size: rem(34);

			transform: translateY(320%);
		}
		@include mq($from: screen-md){
			margin: 0 auto rem(100);
			font-size: rem(38);
			transform: translateY(0);
		}

		@include mq($from: screen-lg){
			font-size: rem(44);
		}

		@include mq($from: screen-xl){
			font-size: rem(60);
		}
	}

	.poster-image {
		display: block;
		width: 100%;
		height: 100%;
		

		position: relative;
		z-index: -1;

		text-align: left;

		@include mq($from: screen-md){
			margin-top: -10%;
		}

		img {
			width: 84%;
			height: auto;

			@include mq($from: screen-sm){
				width: 90%;
			}

			@include mq($from: screen-md){
				width: 84%;
			}
		}
	}

	.button {
		width: 76%;
		padding: rem(20) rem(20);
		margin-top: rem(15);

		display: inline-block;

		background-color: #fff;

		// transform: translateY(-260%);

		@include mq($from: screen-sm){
			width: auto;
			margin-top: 0;
			padding: rem(20) rem(70);
			// max-width: rem(450);
			// margin: auto;

			transform: translateY(-120%);
		}

		@include mq($from: screen-lg){
			transform: translateY(-120%);
		}
	}
}
// .button {
// 	border: rem(1) solid $color-mine-shaft;
// 	box-shadow: rem(10) rem(10) rem(40) rgba(0, 0, 0, .1);
// 	color: $color-mine-shaft;
// 	font-family: $font-stack;
// 	font-size: rem(20);
// 	letter-spacing: rem(3);
// 	padding: rem(20) rem(70);
// 	background: $color-white;
// 	display: block;
// 	text-transform: uppercase;
// 	text-decoration: none;
// 	text-align: center;
// }
</style>



